body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  height: 406px;
  padding: 0;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin-top: 33px;
  padding-bottom: 40px;
}

.card__info {
  margin: 0 16px;
}

.card__title {
  color: #060b35;
  margin: 0;
  font-family: inherit;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin: 4px 0;
  font-size: 10px;
  line-height: 14px;
}

.card__score {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 21px;
  margin: 0 16px;
  padding-bottom: 24px;
  display: flex;
}

.card__reviews, .card__amount {
  color: #060b35;
  font-family: inherit;
  font-size: 10px;
  line-height: 14px;
}

.card__prices {
  justify-content: space-between;
  gap: 85px;
  padding-bottom: 16px;
  display: flex;
}

.card__price {
  color: #616070;
  font-family: inherit;
  font-size: 12px;
  line-height: 18px;
}

.card__cost {
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy {
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 166px;
  height: 40px;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  flex-direction: row;
  gap: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--4 :nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

